import React, { useEffect, useState } from "react";
import Chart from "react-apexcharts";
import { Empty } from "antd";

const GradientDonut = props => {
  const { categories, series } = props;
  const [chartData, setChartData] = useState();

  useEffect(() => {
    const data = {
      series: series?.map(x => Number(x)) ?? [],
      options: {
        chart: {
          type: "donut",
        },
        plotOptions: {
          pie: {
            startAngle: -90,
            endAngle: 270,
          },
        },
        noData: {
          text: "No Data Found",
          align: "center",
          verticalAlign: "middle",
          offsetX: 0,
          offsetY: 0,
          style: {
            color: "#df4759",
            fontSize: "14px",
            fontFamily: undefined,
          },
        },
        labels: categories ?? [],
        fill: {
          type: "gradient",
        },
        legend: {
          formatter: function (val, opts) {
            return val + " - " + opts.w.globals.series[opts.seriesIndex];
          },
        },
        responsive: [
          {
            breakpoint: 480,
            options: {
              chart: {
                width: 200,
              },
              legend: {
                position: "bottom",
              },
            },
          },
        ],
      },
    };
    setChartData(data);
  }, [categories, series]);

  return chartData ? (
    <Chart options={chartData.options} series={chartData.series} width={550} type="donut" />
  ) : (
    <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} />
  );
};

export default GradientDonut;
