import { Card, Row, Col, Empty } from "antd";
import BarChart from "../../../views/app-views/dashboard/Charts/BarChart";
import LineWithLabelChart from "../../../views/app-views/dashboard/Charts/LineWithLabelChart";
import GradientDonut from "../../../views/app-views/dashboard/Charts/GradientDonut";
import HorizontalBarChart from "../../../views/app-views/dashboard/Charts/VerticalBarChart";

const ReportVisualComponent = props => {
  const { list } = props;

  return (
    <>
      {list?.length > 0 ? (
        <Row gutter={[8, 8]}>
          {list?.map(item => {
            var categories = [...item?.labels];
            var series = [...item?.data];
            return (
              <Col className="gutter-row" span={item.type === "LineWithLabelChart" ? 24 : 12}>
                <Card style={{ height: "100%" }}>
                  <Card.Meta title={item.title} description={item.description} style={{ marginBottom: 2 }}></Card.Meta>
                  {item.type === "GradientDonut" && (
                    <div style={{ marginTop: 50 }}>
                      <GradientDonut categories={categories} series={series} />
                    </div>
                  )}
                  {item.type === "LineWithLabelChart" && <LineWithLabelChart categories={categories} series={series} />}
                  {item.type === "BasicBarChart" && <HorizontalBarChart categories={categories} series={series} />}
                </Card>
              </Col>
            );
          })}
        </Row>
      ) : (
        <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} />
      )}
    </>
  );
};

export default ReportVisualComponent;
